.section {
  margin-left: auto;
  margin-right: auto;
}

.about-us {
  &__title {
    font-family: Caveat brush;
    font-size: 24px;
    line-height: 1.25;
    color: $primary-text-color;
    text-align: center;
    margin-bottom: 8px;

    @include for-tablet-only {
      margin-bottom: 16px;
      text-align: initial;
    }
    @include for-desktop-up {
      text-align: initial;
      font-size: 36px;
      line-height: 1.8;
    }
  }

  &__text {
    display: block;
    margin-bottom: 20px;
    font-size: 16px;
    line-height: 1.21;
    transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
    &:hover,
    &:focus {
      transform: scale(1.2, 1.2);
    }
    @include for-desktop-up {
      font-size: 18px;
      line-height: 1.22;
    }
  }

  &__button {
    background-color: transparent;
    border: none;
    padding: 0;
    color: $button-color;
    font-family: Caveat brush;
    font-size: 18px;
    display: block;
    @include for-desktop-up {
      display: none;
    }
  }

  &__wrapper {
    min-width: 279px;

    @include for-tablet-only {
      width: 336px;
    }
    @include for-desktop-up {
      width: 392px;
    }
  }
}

.more {
  margin-bottom: 20px;
  display: none;
  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @include for-desktop-up {
    display: block;
    &:hover,
    &:focus {
      transform: scale(1.2, 1.2);
    }
  }
}

.container-wrapper {
  @include for-phone-only {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  @include for-tablet-only {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  @include for-desktop-up {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
